  src: url("../fonts/galienbold.woff2") format("woff2"), url("../fonts/galienbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: Gilroy;
  src: url("../fonts/gilroy.woff2") format("woff2"), url("../fonts/gilroy.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Open Sans';
