body {
  min-width: 320px;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  font-family: Gilroy, Arial, sans-serif;
  font-weight: 300;
  letter-spacing: 0.05em;
  color: white;
  word-wrap: break-word;
  text-align: left;
  background-position: top center;
  background-repeat: no-repeat; }

@media (max-width: 459px), (min-width: 806px) {
  body {
    font-size: 16px;
    line-height: 26px; } }

@media (min-width: 460px) and (max-width: 659px) {
  body {
    font-size: 17px;
    line-height: 27px; } }

@media (min-width: 660px) and (max-width: 805px) {
  body {
    font-size: 18px;
    line-height: 28px; } }

img {
  max-width: 100%;
  height: auto; }

a {
  color: inherit;
  text-decoration: none; }

a:hover {
  outline: none; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.sprt-cont {
  display: none; }

